<template>
    <div class="content__wrapper">
        <div class="container">
            <div class="content__top">
                <div class="btn content__btn" @click="filterFilms('Просмотрено')">Просмотренные</div>
                <div class="btn content__btn" @click="filterFilms('В очереди')"> В очереди </div>
                <div class=" btn content__btn" @click="filterFilms('Предложенные')"> Предложенные
                </div>
            </div>
            <div class="content__bottom">
                <div class="content__bottom-movies">
                    <movieCard class="film" v-for="item, index in this.films.setFilms" :key="index" :id="item.id"
                        :film-name="item.filmName" :img-path="item.imgPath" :Status="item.status" :obj-rate="item.ranks"
                        :Year="item.year">
                        {{ item.desc }}
                    </movieCard>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { useContent } from '@/stores/filmsStore';

import movieCard from '../elements/movieCard.vue';
export default {
    components: {
        movieCard,
    },
    data() {
        return {
            films: [],
        }
    },
    methods: {
        filterFilms(status) {
            this.films.getFiltredFilms(status);
        }
    },
    created() {
        this.films = useContent();
    }

}

</script>

