<template>
    <router-link :to="'/film_' + id" class="card__link">
        <div class="card__wrapper">
            <img loading="lazy" :src="imgPath" alt="filmImg" class="card__img">
            <div class="card__filmName">{{ filmName }} <br> <span class="card__year" style="font-size: 14px;"> {{ Year
                    }}
                    года</span></div>

            <div class="card__filmStatus">{{ Status }}</div>
            <div class="card__rate" v-if="this.objRate" :class="dynamicClass"> {{ caclulateRate() }}</div>
        </div>
    </router-link>
</template>
<script>
export default {
    data() {
        return {

        }
    },
    props: {
        "id": {
            type: Number,
        },
        "filmName": {
            type: String,
        },
        "imgPath": {
            type: String,
        },
        "Year": {
            type: Number,
            default: 0,
        },
        "Status": {
            type: String,
        },
        "objRate": {
            type: Object,
            default: () => {
                return
            },
        }
    },
    methods: {
        caclulateRate() {
            let returnSum = 0;
            let people = 0;
            for (let key in this.objRate) {
                returnSum += this.objRate[key];
                people++
                /* console.log(`${this.filmName} и ${this.objRate[key]}`) */
            }

            return (returnSum / people).toFixed(1);
        }
    },
    computed: {
        dynamicClass() {

            let allRate = this.caclulateRate()
            if (allRate <= 4) {
                return 'red';
            }
            else if (allRate <= 7) {
                return 'yellow';
            }
            else {
                return 'green';
            }

        }
    },
    created() {
        this.caclulateRate();
    }

}
</script>
<style scoped>
.card__link {
    display: flex;
    flex: 0 0 20%;
    text-decoration: none;
}

.card__wrapper {
    display: flex;
    flex-direction: column;
    cursor: pointer;
    flex: 0 0 20%;
    position: relative;
    padding: 20px 15px;
    background: #119686;
    border-radius: 20px;
}

.card__img {
    width: 220px;
    max-width: 220px;
    height: 300px;
    margin-bottom: 20px;
    border-radius: 20px;
}

.card__filmName {
    display: block;
    width: fit-content;
    font-weight: 700;
    font-size: 24px;
    color: #fff;
}

.card__year {
    display: inline-block;
    margin-bottom: 10px;
}


.card__filmStatus {
    color: #fff;
    margin-top: auto;
    width: fit-content;
}


.card__rate {
    color: #fff;
    position: absolute;
    padding: 7px;
    border-radius: 50%;
    bottom: 13px;
    right: 20px;
    font-weight: 700;
    font-size: 14px;
}

.red {
    background: rgb(252, 10, 10);

}

.yellow {
    background: gray;

}

.green {
    background: rgb(2, 175, 2);

}
</style>
